import fetchIpInfo from './API/ipAddress';
import { useEffect, useState } from "react";
import { Footer } from './Footer';
import Map from './Map';
import OutputBox from './OutputBox';
function App() {

  const [ipInfo, setIpInfo] = useState({
    ipAddress: "",
    country: "",
    city: "",
    lat: "",
    lng: "",
    postalCode: "",
    timeZone: "",
    isp: "",
    region: ""
  })
  const [input, setInput] = useState("")
  const { lat, lng, ipAddress } = ipInfo

  useEffect(() => {
      getIP()
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  async function getIP() {
    setIpInfo({
      ...ipInfo,
      ipAddress: "",
      country: "",
      city: "",
      lat: "",
      lng: "",
      postalCode: "",
      timeZone: "",
      isp: "",
      region: "",
    })
    const data = await fetchIpInfo(input)
    if (data) {
      setIpInfo({
        ...ipInfo,
        ipAddress: data.ip,
        country: data.location.country,
        city: data.location.city,
        lat: data.location.lat,
        lng: data.location.lng,
        postalCode: data.location.postalCode,
        timeZone: data.location.timezone,
        isp: data.isp,
        region: data.location.region
      })
      console.log(data);
      setInput("")
    }
  }
  const handleChange = e => {
    const value = e.target.value
    setInput(value)
  }
  const handleClick = () => {
    if (input === "") {
      return
    }
    getIP()
  }

  return (
    <div className="App">
      {!ipAddress && <div className="loader"></div>}
      <header className="App-header">
        <h1>IP Address Tracker</h1>
        <div className="form">
          <input type="text" className="ip-input" placeholder="Search for any IP address or domain" value={input} onChange={handleChange} />
          <button onClick={handleClick} className="submit-btn" >{">"}</button>
        </div>
        <OutputBox ipInfo={ipInfo} />
      </header>
      <Map lng={lng} lat={lat} />
      <Footer />
    </div>
  );
}

export default App;
