import axios from "axios";

async function fetchIpInfo(ipAddress = "") {
    const apiKey = "at_yPXgalwFDVNmC7SgLo7mjI1lkG09f"
    const url = `https://geo.ipify.org/api/v1?apiKey=${apiKey}&ipAddress=${ipAddress}&domain=${ipAddress}`
    try {
        const response = await axios.get(url);
        return response.data
    } catch (error) {
        console.error(error);
    }
}
export default fetchIpInfo