const OutputBox = ({ ipInfo }) => {
    const { ipAddress, country, city, timeZone, isp, region, postalCode } = ipInfo
    return (
        <div className="output-box">
            <div className="output-blocks">
                <span className="label">IP Address</span>
                <span className="value">{ipAddress}</span>
            </div>
            <div className="line"></div>
            <div className="output-blocks">
                <span className="label">Location</span>
                <span className="value">{city}, {country}<br /> {postalCode ? postalCode : region}</span>
            </div>
            <div className="line"></div>
            <div className="output-blocks">
                <span className="label">Time zone</span>
                <span className="value">UTC {timeZone}</span>
            </div>
            <div className="line"></div>
            <div className="output-blocks">
                <span className="label">ISP</span>
                <span className="value">{isp}</span>
            </div>
        </div>
    )
}

export default OutputBox
